<!-- src/views/Blog.vue -->
<template>
  <div class="blog content-container">
    <div class="controls">
      <div class="sort-options">
        <span>Sort:</span>
        <button 
          v-for="option in sortOptions" 
          :key="option.value"
          @click="currentSort = option.value"
          :class="{ active: currentSort === option.value }"
        >
          <i :class="['fas', option.icon]"></i> {{ option.label }}
        </button>
      </div>
      <div class="view-toggle">
        <span>View:</span>
        <button 
          v-for="view in viewOptions" 
          :key="view.value"
          @click="currentView = view.value"
          :class="{ active: currentView === view.value }"
        >
          <i :class="['fas', view.icon]"></i>
        </button>
      </div>
    </div>

    <div v-if="isLoading" class="loading">Loading blog posts...</div>
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else :class="['blog-list', currentView + '-view']">
      <BlogItem
        v-for="post in sortedBlogPosts"
        :key="post.slug"
        :post="post"
        :currentView="currentView"
        @click="navigateToBlogPost(post.slug)"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import BlogItem from '@/components/BlogItem.vue'

export default {
  name: 'Blog',
  components: { BlogItem },
  setup() {
    const store = useStore()
    const router = useRouter()
    const currentSort = ref('date')
    const currentView = ref('compact')
    const isLoading = ref(true)
    const error = ref(null)

    const sortOptions = [
      { value: 'date', label: 'Date', icon: 'fa-calendar-alt' },
      { value: 'title', label: 'Title', icon: 'fa-sort-alpha-down' },
      { value: 'progress', label: 'Progress', icon: 'fa-tasks' }
    ]

    const viewOptions = [
      { value: 'compact', icon: 'fa-bars' },
      { value: 'list', icon: 'fa-list' },
      { value: 'grid', icon: 'fa-th-large' }
    ]

    const loadBlogPosts = async () => {
      try {
        await store.dispatch('content/loadBlogPosts')
        isLoading.value = false
      } catch (err) {
        console.error('Failed to load blog posts:', err)
        error.value = 'Failed to load blog posts. Please try again later.'
        isLoading.value = false
      }
    }

    onMounted(loadBlogPosts)

    onUnmounted(() => {
      // Clean up any subscriptions or timers if needed
    })

    const blogPosts = computed(() => store.getters['content/getBlogPosts'])

    const sortedBlogPosts = computed(() => {
      if (!blogPosts.value) return []
      return [...blogPosts.value].sort((a, b) => {
        switch (currentSort.value) {
          case 'date':
            return new Date(b.date) - new Date(a.date)
          case 'title':
            return a.title.localeCompare(b.title)
          case 'progress':
            return (b.progress || 0) - (a.progress || 0)
          default:
            return 0
        }
      })
    })

    const navigateToBlogPost = (slug) => {
      router.push({ name: 'BlogPost', params: { slug } })
    }

    return {
      currentSort,
      currentView,
      sortOptions,
      viewOptions,
      sortedBlogPosts,
      navigateToBlogPost,
      isLoading,
      error
    }
  }
}
</script>
<style scoped>
@import '../assets/css/tab-view-styles.css';

.blog {
  padding: var(--spacing-medium);
}

h2 {
  margin-top: var(--spacing-large);
  margin-bottom: var(--spacing-medium);
  color: var(--highlight-color);
}

.blog-list + h2 {
  margin-top: var(--spacing-large);
}

.blog-list {
  margin-top: var(--spacing-medium);
}

/* Add hover effect for clickable items */
.blog-list > * {
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.blog-list > *:hover {
  background-color: var(--hover-color, #f0f0f0);
}
.loading, .error {
  text-align: center;
  padding: var(--spacing-large);
  font-size: var(--font-size-large);
  color: var(--text-color-secondary);
}

.error {
  color: var(--error-color, #ff0000);
}
</style>
