<!-- src/components/AboutTab.vue -->
<template>
  <div class="about-tab">
    <div v-if="tab === 'overview'" class="overview">
      <p v-for="(paragraph, index) in overviewParagraphs" :key="index">{{ paragraph }}</p>
    </div>
    <div v-else-if="tab === 'favorites'" class="favorites">
      <div v-if="!favoritesExist" class="no-favorites">
        <p>No favorites found. Please add some favorites to see them here.</p>
      </div>
      <template v-else>
        <div class="controls">
          <div class="view-toggle">
            <span>View:</span>
            <button 
              v-for="view in viewOptions" 
              :key="view.value"
              @click="changeView(view.value)"
              :class="{ active: currentView === view.value }"
            >
              <i :class="['fas', view.icon]"></i>
            </button>
          </div>
        </div>

        <div v-for="(items, category) in favorites" :key="category" class="favorite-category">
          <h2>{{ formatCategory(category) }}</h2>
          <div v-if="items && items.length > 0" :class="['favorite-list', currentView + '-view']">
            <FavoriteItem
              v-for="item in items"
              :key="item.name"
              :item="item"
              :category="category"
              :currentView="currentView"
              @toggleShowcase="toggleShowcase"
            />
          </div>
          <p v-else>No items found in this category.</p>
        </div>
      </template>
    </div>
    <ShowcaseSidebar
      v-if="showcaseItem"
      :item="showcaseItem"
      :category="showcaseCategory"
      :isOpen="showShowcase"
      @close="closeShowcase"
    />
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import FavoriteItem from '@/components/FavoriteItem.vue'
import ShowcaseSidebar from '@/components/ShowcaseSidebar.vue'

export default {
  name: 'AboutTab',
  components: { FavoriteItem, ShowcaseSidebar },
  props: {
    tab: {
      type: String,
      required: true
    },
    overview: {
      type: String,
      required: true
    },
    favorites: {
      type: Object,
      required: true
    }
  },
  setup(props) {
    const currentView = ref('grid')
    const showcaseItem = ref(null)
    const showcaseCategory = ref('')
    const showShowcase = ref(false)

    const overviewParagraphs = computed(() => props.overview.split('\n\n'))

    const favoritesExist = computed(() => {
      return Object.keys(props.favorites).length > 0 && 
             Object.values(props.favorites).some(category => category && category.length > 0)
    })

    const viewOptions = [
      { value: 'compact', icon: 'fa-bars' },
      { value: 'list', icon: 'fa-list' },
      { value: 'grid', icon: 'fa-th-large' }
    ]

    const formatCategory = (category) => {
      return category.split(/(?=[A-Z])/).join(' ')
    }

    const changeView = (newView) => {
      currentView.value = newView
    }

    const toggleShowcase = (item, category) => {
      if (showcaseItem.value === item) {
        closeShowcase()
      } else {
        showcaseItem.value = item
        showcaseCategory.value = category
        showShowcase.value = true
      }
    }

    const closeShowcase = () => {
      showShowcase.value = false
      setTimeout(() => {
        showcaseItem.value = null
        showcaseCategory.value = ''
      }, 300) // Wait for the transition to finish before clearing the item
    }

    return {
      currentView,
      overviewParagraphs,
      viewOptions,
      formatCategory,
      changeView,
      favoritesExist,
      showcaseItem,
      showcaseCategory,
      showShowcase,
      toggleShowcase,
      closeShowcase
    }
  }
}
</script>
<style scoped>
@import '../assets/css/tab-view-styles.css';

h2 {
  margin-top: var(--spacing-large);
  margin-bottom: var(--spacing-medium);
  color: var(--highlight-color);
}

.favorite-list + h2 {
  margin-top: var(--spacing-large);
}

.no-favorites {
  text-align: center;
  padding: var(--spacing-large);
  color: var(--text-color-secondary);
}

.overview p {
  margin-bottom: var(--spacing-medium);
}
</style>
